<template>
	<div class="task">
		<div class="task-item" v-for="(item,index) in todolist" :key="index">
			<p><span :class="{'fcolor':item.status==true}">{{item.name}}</span><span><a href="#" @click="del(index)">删除</a><a href="#" @click="complete(index)">完成</a></span></p>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{	
				todolist:this.$store.getters.todolist,
			}
		},
		methods:{
			del(index){
				this.$store.getters.todolist.splice(index,1);
			},
			complete(index){
				this.$store.commit('completelist',{data:this.todolist[index],status:index})
			}
		}
	}
</script>

<style scoped>
	.task-item{
		width: 80%;
		padding: 10px 0;
		border-radius: 5px;
		box-shadow: 0 1px 2px rgba(0, 0, 0, .1);
		margin: 1% auto;
	}
	
	.task-item p{
		margin: 0;
		display: flex;
		justify-content: space-around;
	}
	.task-item span{
		display: block;
		width: 15%;
		overflow: hidden; 
		text-overflow: ellipsis; 
		white-space:nowrap;
	}
	.task-item p a{
		text-decoration: none;
		color: #757575;
	}
	.task-item p a:last-child{
		margin-left: 10%;
	}
	.fcolor{
		text-decoration: line-through;
		opacity: .4;
	}
	@media  screen and (max-width:768px) {
		.task{
			padding-top: 18%;
		}
		.task-item span{
			display: block;
			width: 30%;
		}
	}
</style>
